import React from "react";
import { Link, useLocation } from "react-router-dom";
import styles from "./DetailPage.module.css";

const DetailPage = () => {
  const location = useLocation();
  console.log(location.state);
  return (
    <div className={styles.wrapper}>
      <Link className={styles.backBtn} to="/">
        Back
      </Link>
      <div className={styles.detailContainer}>
        <img className={styles.flag} src={location.state.flag} alt="flag" />
        <div className={styles.content}>
          <h1>{location.state.name}</h1>
          <div className={styles.subContent}>
            <div className={styles.middle}>
              <div className={styles.left}>
                <p>
                  Native Name:{" "}
                  <span className={styles.text}>
                    {
                      location.state.natName[
                        Object.keys(location.state.natName)[0]
                      ].common
                    }
                  </span>
                </p>
                <p>
                  Population:{" "}
                  <span className={styles.text}>
                    {location.state.population.toLocaleString()}
                  </span>
                </p>
                <p>
                  Region:{" "}
                  <span className={styles.text}>{location.state.region}</span>
                </p>
                <p>
                  Sub Region:{" "}
                  <span className={styles.text}>
                    {location.state.subregion}
                  </span>
                </p>
                <p>
                  Capital:{" "}
                  <span className={styles.text}>{location.state.capital}</span>
                </p>
              </div>
              <div className={styles.right}>
                <p>
                  Top Level Domain:{" "}
                  <span className={styles.text}>{location.state.tld}</span>
                </p>
                <p>
                  Currencies:{" "}
                  <span className={styles.text}>
                    {location.state.currencies === undefined
                      ? " "
                      : [Object.keys(location.state.currencies)[0]].name}
                  </span>
                </p>
                <p>
                  Languages:{" "}
                  <span className={styles.text}>
                    {Object.values(location.state.languages).join(",")}
                  </span>
                </p>
              </div>
            </div>
            <div className={styles.bottom}>
              <div>
                <p>Border Countries: </p>
              </div>
              {location.state.borders === undefined ? (
                <p className={styles.box}>No Borders</p>
              ) : (
                <div className={styles.border}>
                  {location.state.borders.map((item) => (
                    <p key={item} className={styles.box}>
                      {" "}
                      {item}
                    </p>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DetailPage;
