import React, { useState } from "react";
import Countries from "./Countries";
import styles from "./AllCountries.module.css";
import { SearchBar } from "./SearchBar";
import ReactLoading from "react-loading";
import Select, { Option } from "rc-select";

export const AllCountries = ({ isLoading, cinfo }) => {
  const [searchItem, setSearchItem] = useState("");
  const [option, setOption] = useState("");

  const updateOption = (value) => {
    setOption(value);
  };

  const updateSearchItem = (value) => {
    setSearchItem(value);
  };
  return (
    <div className={styles.app}>
      <div className={styles.search}>
        <div>
          <SearchBar onChange={updateSearchItem} />
        </div>
        <div className={styles.def}>
          <select
            onChange={(e) => updateOption(e.target.value)}
            name="filter-opt"
            id="filter"
            className={styles.opt}
          >
            <option value="">Filter by Region</option>
            <option value="Africa">Africa</option>
            <option value="America">America</option>
            <option value="Asia">Asia</option>
            <option value="Europe">Europe</option>
            <option value="Oceania">Oceania</option>
          </select>
        </div>
      </div>
      <div className={styles.mainContainer}>
        <div className={styles.mainSection}>
          {isLoading ? (
            <div className={styles.loadingContainer}>
              <ReactLoading type={"spinningBubbles"} color={"grey"} />{" "}
            </div>
          ) : (
            <div className={styles.content}>
              {cinfo
                .filter((inf) =>
                  inf.name.common
                    .toLowerCase()
                    .includes(searchItem.toLocaleLowerCase())
                )
                .filter((val) =>
                  val.region.toLowerCase().includes(option.toLocaleLowerCase())
                )
                .map((info) => (
                  <Countries
                    key={info.name.common}
                    flag={info.flags.png}
                    name={info.name.common}
                    population={info.population}
                    region={info.region}
                    capital={info.capital}
                    subregion={info.subregion}
                    tld={info.tld}
                    languages={info.languages}
                    natName={info.name.nativeName}
                    currencies={info.currencies}
                    borders={info.borders}
                  />
                ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
